<div class="hello"><div class="ui row" v-if="!uid"><div class="sixteen wide column"> <div class="ui huge buttons"><button class="ui orange button" @click="toggleLogin"><!-- i.google.icon-->{{ $t('login.login') }}</button></div></div></div><div class="ui container" v-if="users &amp;&amp; toList(users).length &gt; 0"><div class="ui grid"><div class="row ui form" v-show="uid &amp;&amp; users[uid]"><div class="field"><div class="ui labeled input"><label class="ui label">{{ $t('groups.group_name') }}</label><input type="text" v-model="newName" @input="filterInput('newName', $event)" :placeholder="$t('groups.enter_group_name')"></div></div><div class="field button-field"><a class="ui green button" :class="{disabled: !newName}" @click="addGroup()">{{ $t('groups.create_group') }}</a></div></div><div class="ui two stackable column row"><div class="ui eight wide column ui segment" v-for="(g, idx) in searchBy(groups, mySearch)" :key="g.idx" v-show="!g.hidden"><h3>〈{{g.n}}〉</h3><p>{{g.intro}}<br class="thin-only">&nbsp;&nbsp;&nbsp;&nbsp;</p><!--  a(@click="edit = !edit")i.edit.icon
| {{edit ? '結束' : ''}}編輯社團資料--><p><div class="ui buttons"><router-link class="ui basic green button" :to="'/group/' + g.idx"><i class="sign-in icon"></i>{{$t('groups.go_group')}}</router-link><a class="ui green button" v-if="isUser(uid) &amp;&amp; !isMember(g.idx)" @click="join(g.idx)">{{$t('groups.join_group')}}</a><a class="ui red basic button" v-if="isUser(uid) &amp;&amp; isMember(g.idx)" @click="out(g.idx)">{{ $t('groups.out_group') }}</a></div></p><!--.ui.form(v-show="edit").field(v-if="!uid")
