  .ui.labeled.input
    label.ui.label 輸入社團簡介  
    input(type="text", v-model="newIntro", placeholder="請先輸入社團簡介")
//.field
  a.ui.green.button(:class="{disabled: !newIntro}", @click="addIntro(idx)")
    | 更新簡介--><div class="ui grid"><div class="row"><p>{{$t('groups.members')}}<span v-for="m in g.members"><router-link :to="'/flag/' + m" v-if="isUser(m)"><img class="ui avatar" :src="users[m].photoURL" :alt="users[m].name"></router-link></span></p></div></div></div></div></div></div></div>