<div class="hello"><div class="ui row" v-if="!uid &amp;&amp; (!users || toList(users).length == 0)"><div class="sixteen wide column"> <div class="ui huge buttons"><button class="ui orange button" @click="toggleLogin"><!-- i.google.icon-->{{ $t('login.login') }}</button></div></div></div><loader v-if="users &amp;&amp; toList(users).length &gt; 0 &amp;&amp; groups[$route.params.idx] &amp;&amp; !groups[$route.params.idx].hidden"><div class="ui grid"><div class="ui one column row" v-if="groups[$route.params.idx].n"><div class="ui column ui segment"><h3>〈{{groups[$route.params.idx].n}}〉</h3><p>{{groups[$route.params.idx].intro}}<br class="thin-only">&nbsp;&nbsp;&nbsp;&nbsp;<a @click="toggleEdit($route.params.idx)" v-if="isMember(groups[$route.params.idx].idx)"><i class="edit icon"></i>{{edit ? $t('login.end') : ''}}{{ $t('group.edit_group') }}</a></p><p><router-link class="ui basic green button" to="/groups"><i class="globe icon"></i>{{$t('group.explore_groups')}}</router-link></p><div class="ui form" v-show="edit"><div class="field" v-if="!uid"><button class="ui orange button" @click="loginGoogle()"><i class="google icon"></i>{{$t('login.login_first')}}</button></div><div class="field"><div class="ui labeled input"><label class="ui label">{{$t('group.enter_intro')}}</label><textarea v-model="newIntro" @input="filterInput('newIntro', $event)" :placeholder="$t('group.enter_intro_first')" rows="5" style="width: 100%"></textarea></div></div><div class="field"><a class="ui green button" :class="{disabled: !newIntro}" @click="addIntro($route.params.idx)">{{$t('group.update_intro')}}</a></div></div><p v-if="(groups[$route.params.idx].members || []).length === 0"><button class="ui red button" @click="hideGroup($route.params.idx)"><i class="delete icon"></i>{{ $t('group.delete_group') }}</button></p><div class="ui grid"><div class="row"><p>成員：<span v-for="m in groups[$route.params.idx].members"><router-link :to="'/flag/' + m" v-if="isUser(m)"><img class="ui avatar" :src="users[m].photoURL" :alt="users[m].n"></router-link></span><span v-if="uid"><a class="ui green tiny button" v-if="isUser(uid) &amp;&amp; !isMember(groups[$route.params.idx].idx)" @click="join(groups[$route.params.idx].idx)">{{$t('groups.join_group')}}</a><a class="ui red tiny button" v-if="isUser(uid) &amp;&amp; isMember(groups[$route.params.idx].idx)" @click="out(groups[$route.params.idx].idx)">{{$t('groups.out_group')}}</a></span></p></div><div class="two column stackable row"><div class="column"><div class="ui divided list"><div class="item left aligned">{{$t('group.resources')}}</div><div class="item left aligned" v-for="(r, index) in groups[$route.params.idx].res" :key="index + r.n + r.href" v-show="!r.hidden || edit"><a :href="r.href" target="_blank" rel="noopener noreferrer"><img :src="'http://www.google.com/s2/favicons?domain=' + r.href" :alt="r.n">{{r.n}}</a><div class="filler"></div><a class="ui basic green button" v-if="edit &amp;&amp; r.hidden" @click="showResource($route.params.idx, index)"><i class="eye icon"></i>{{$t('group.show_resource')}}</a><a class="ui basic red button" v-if="edit &amp;&amp; !r.hidden" @click="hideResource($route.params.idx, index)"><i class="hide icon"></i>{{$t('group.hide_resource')}}</a><!-- 新增喜歡按鈕--><a class="ui blue button" v-if="isUser(uid) &amp;&amp; !(r.likes || []).includes(uid)" @click="likeResource($route.params.idx, index)"><i class="thumbs up icon"></i>{{$t('group.like')}}</a><span class="red" v-if="r.likes &amp;&amp; r.likes.length &gt; 0"><i class="heart icon"></i>{{r.likes.length}} {{$t('group.likes')}}</span></div><div class="item ui form" v-show="uid &amp;&amp; edit"><div class="field"><div class="ui labeled input"><label class="ui label">{{$t('group.enter_resource')}}</label><input type="text" v-model="newResName" @input="filterInput('newResName', $event)" :placeholder="$t('group.enter_resource_first')"></div></div><div class="field"><div class="ui labeled input"><label class="ui label">{{$t('group.enter_link')}}</label><input type="text" v-model="newHref" @input="filterInput('newHref', $event)" :placeholder="$t('group.enter_link_first')"></div></div><div class="field"><a class="ui green button" :class="{disabled: !newHref || !newResName}" @click="addRes($route.params.idx)">{{$t('group.add_resource')}}</a></div></div></div></div><div class="column">{{ $t('login.leave_messages') }}<div class="ui divided list"><div class="item" v-for="(c, index) in latestChats" :key="index"><img class="ui avatar" :src="c.photoURL">{{c.n}} : {{c.t}}</div><div class="item ui form" v-if="isMember(groups[$route.params.idx].idx)"><div class="field"><div class="ui labeled input"><img class="ui avatar" :src="photoURL"><input class="input" v-model="msg" @input="filterInput('msg', $event)" :placeholder="$t('group.anything_to_say')"><a class="ui label green button" :class="{disabled: !msg}" @click="addChat($route.params.idx)">{{ $t('login.leave_messages') }}  </a></div></div></div></div></div></div></div></div></div></div></loader></div>